import { ref, onMounted } from "vue";

export function getPackage() {
  const packages = ref([]);

  const getPack = async () => {
    await fetch("https://v2-deeper-vision-system.codebase-product.com/api/home")
      .then((res) => res.json())
      .then((data) => (packages.value = data.page.packages));
  };

  onMounted(getPack);

  return { packages, getPack };
}
